.post {
    width: 385px;
    /* margin: 0px 25px 40px 25px; */
    margin:10px;
    display: flex;
    flex-direction: column;
    box-shadow:  1px 3px 20px rgb(0 0 0 / 10%);
}

.postImg {
    width: 385px;
    height: 280px;
    object-fit: contain;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
}

.postInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCat {
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 11px;
    color: #be9656;
    line-height: 19px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}
.postCats
{
    margin-top: 10px;
}

.postTitle {
    font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
    font-size: 24px;
    font-weight: 900;
    margin-top: 15px;
    cursor: pointer;
}

.postDate {
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 13px;
    font-weight: 400;
    color: #999999;
    margin-top: 15px;
}

.postDesc {
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #444444;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    padding: 0 5px;
}
@media screen and (max-width:650px)
{
    .post
    {
        width: 100%;
        /* margin: 0px 25px 40px 25px; */
        margin:4px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}