.write {
    padding-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.writeImg {
    /* margin-left: 150px; */

    /* width: 100vw; */
    margin-bottom: 10px;
    height: 250px;
    border-radius: 10px;
    object-fit: contain;
    /* box-shadow: 2px -1px 11px 17px #dad2d2f7; */
}

.writeForm {
    /* position: relative; */
    width: 100vw;
}

.writeFormGroup {
    /* margin-left: 150px; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.writeIcon {
    width: 25px;
    height: 25px;
    font-size: 20px;
    border: 1px solid;
    border-radius: 50%;
    color: rgb(129, 125, 125);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.writeInput {
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput::placeholder {
    color: rgb(189, 185, 185);
    font-weight: 400;
}

.writeInput:focus {
    outline-style: none;
}

.writeText {
    width: 70vw;
    height: 100vh;
    font-family: inherit;
    font-size: 20px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.writeText::-webkit-scrollbar
{
    display: none;
}


.writeSubmit {
    /* position: absolute; */
    top: 20px;
    right: 50px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    align-items: center;
}


@media screen and (max-width:650px) {
    .writeImg {
        width: 100vw;
        object-fit: cover;
    }
    .writeText
    {
        width: 100vw;
    }
    .writeIcon
    {
        margin-left: 15px;
    }
    .writeSubmit
    {
        right: 25px;
    }

}